@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&family=Roboto:wght@400;700&family=Raleway&display=swap');
nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    z-index: 5;
    height: 90px;
    top: 0%;
    background: linear-gradient(to right, rgb(255, 255, 255), rgb(72, 27, 60));
    overflow: hidden;
    width: 99vw;
}

nav ul li {
    display: inline-block;
    list-style: none;
    margin: 10px 50px;
    color: black;
}

nav ul li a {
    color: rgb(255, 255, 255);
    text-decoration: none;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
    position: relative;
}

nav ul li a::after {
    content: "";
    width: 0;
    height: 4px;
    background-color: rgba(246, 189, 227, 0.515);
    position: absolute;
    left: 0;
    bottom: -8px;
    transition: 0.75s;
}

nav ul li a:hover::after {
    width: 100%;
}

.logo {
    width: 200px;
    height: fit-content;
    margin-left: 20px;
}

.main-content h1 {
    margin-top: 7rem;
    font-family: 'Poppins';
